.App {
  text-align: center;
}

table {
  font-family: arial, sans-serif;
  font-size: 80%;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #dddddd;
}

.mapboxgl-popup-content {
  overflow: auto;
}